<template>
    <div class="page">
        <!-- <div class="marquee">
            <van-notice-bar
  left-icon="volume-o"
  text="Hello PCD"
/>
        </div> -->
        <div class="pages">
            <!--  -->
            <div class="nav">
                <div class="logo"><img src="./img/logo.png" alt="" srcset=""></div>
                <div class="navList">
                    <a href="" class="isShow">DAO</a>
					<!-- <a href="#" @click="navFlag = true"><svg t="1714794810902" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="12817" width="200" height="200"><path d="M127.9 168.9h768v46h-768zM127.2 809.1h768v46h-768zM408.1 495.5h488.7v46H408.1zM317.6 346.3L130.1 522.6l187.5 176.3V346.3z m-46 246.2l-74.3-69.9 74.3-69.9v139.8z" p-id="12818" fill="#ffffff"></path></svg></a> -->
                </div>

            </div>
            <!--  -->
            <!-- <div class="navB" v-show="navFlag">
                <div class="navTwo">
                    <div class="logo"><img src="./img/logo.png" alt="" srcset=""></div>
                    <div class="navList">
                        <a href="#" @click="navFlag = false"><svg t="1714798443381" class="icon" viewBox="0 0 1024 1024"
